import React from "react";
import "./App.css";
import Navbar from "./components/Navbar/index";
import Footer from "./components/Footer/index";
import Home from "./pages/Home/index";
import About from "./pages/About/index";
import Contact from "./pages/Contact/index";
import Projects from "./pages/Projects/index";
import { Route, Routes } from "react-router-dom";
function App() {
  return (
    <div className="App">
      <Navbar />

      <div>
        <Routes>
          <Route exact path="/react-portfolio" id="home" element={<Home />} />
          <Route path="/react-portfolio/about" id="about" element={<About />} />
          <Route path="/react-portfolio/contact" id="contact" element={<Contact />} />
          <Route path="/react-portfolio/projects" id="projects" element={<Projects />} />
        </Routes>
      </div>
      <Footer className="footer" />
    </div>
  );
}

export default App;
